import React, { lazy, Suspense } from 'react';

const Table = lazy(() => import('./Table'));

const columns = ['Country', 'Daily Cases', 'Daily Deaths', 'Total Tests'];

const NationDailyRecords = ({ nationDailyRecords }) => {
  return (
    <Suspense fallback={<div />}>
      <Table columns={columns} rows={nationDailyRecords}></Table>
    </Suspense>
  );
};

export default NationDailyRecords;
